import React, { useState } from 'react';
import './form.css';
import Axios from 'axios';
import { withTranslation } from 'react-i18next';
import { render } from 'react-dom';

const Form=(props)=> {

  const [data, setData] = useState({
    Name: "",
    Email: "",
    Phone: "",
    Holder: ""
  });

  const [isSubmitSuccess, setIsSubmitSuccess] = useState(false);

  function getQueryVariable(variable) {
    var query = window.location.search.substring(1);
    console.log(query)//"this should be everything after the ?"
    var vars = query.split("&");
    console.log(vars) //[ 'app=article', 'act=news_content', 'aid=160990' ]
    for (var i = 0; i < vars.length; i++) {
      var pair = vars[i].split("=");
      console.log(pair)//[ 'app', 'article' ][ 'act', 'news_content' ][ 'aid', '160990' ]
      if (pair[0] == variable) { return pair[1]; }
    }

    return (false);
  }


  function submit(e) {
    e.preventDefault();
    const url = `https://api.nivesh.com/API/WebAddLeadPartner`;

    const payload = {
      Name: data.Name,
      PhoneNo: data.Phone,
      Email: data.Email,
      Message: data.Holder,
      IsDistributor: 0,
      TypeRequest: `LeadPartnerForm|${getQueryVariable('utm_campaign')}|${getQueryVariable('utm_content')}|${getQueryVariable('utm_medium')}|${getQueryVariable('utm_source')}`
    };

    const headers = {
      headers: { 'Content-Type': 'application/json' }
    };

    Axios.post(url, payload, headers)
      .then(res => {
        setIsSubmitSuccess(true)
      });
  }

  const handle = (e) => {
    const userInput = { ...data }
    userInput[e.target.id] = e.target.value
    setData(userInput);
  }

  const{t} = props;

    return (
      <div className="col-lg-4 col-md-6 col-12">
        <div className="formKnow">
          <h4>{t('CTAFooter:formText2')}</h4>
          {!isSubmitSuccess && <form onSubmit={(e) => submit(e)}>
            <div className="form-group">
              <input onChange={(e) => handle(e)} type="text" className="form-control" id="Name" value={data.Name} placeholder={t('CTAFooter:formText4')} required />
            </div>
            <div className="form-group">
              <input onChange={(e) => handle(e)} type="email" className="form-control" id="Email" value={data.Email} placeholder={t('CTAFooter:formText5')} required />
            </div>
            <div className="form-group">
              <input onChange={(e) => handle(e)} type="text" className="form-control" id="Phone" value={data.Phone} placeholder={t('CTAFooter:formText6')} required />
            </div>
            <div className="radioKonw">
              <span>{t('CTAFooter:formText7')}</span>
              <div className="form-check">
                <input onChange={(e) => handle(e)} className="form-check-input" type="radio" name="flexRadioDefault" id="Holder" value="ARN Holder" required />
                <label className="form-check-label" for="Holder">
                {t('CTAFooter:formText8')}
                </label>
              </div>
              <div className="form-check">
                <input onChange={(e) => handle(e)} className="form-check-input" type="radio" name="flexRadioDefault" id="Holder" value="NON ARN Holder" required />
                <label className="form-check-label" for="Holder" >
                {t('CTAFooter:formText9')}
                </label>
              </div>

            </div>
            <div className="form-check">
              <input className="form-check-input" type="checkbox" value="" id="flexCheckDefault" required checked />
              <label className="form-check1-label" for="flexCheckDefault">
              {t('CTAFooter:formText10')}
              </label>
            </div>
            <button className="btn btn-primary">{t('CTAFooter:formText11')}</button>
          </form>}

          {isSubmitSuccess && <div><p>Your form is successfully submitted. Our representative will get in touch with you shortly.</p></div>}
        </div>
      </div>
    );
}

export default withTranslation()(Form)
