import React, { Component } from "react";
import Form from "../../Component/Form/form";
import Testimonials from "../../Component/Testimonials/Testimonials";
import { withTranslation } from "react-i18next";
import {Helmet} from 'react-helmet';

export class Grow extends Component {

  constructor() {
    super()

    this.state = {
      items: undefined
    }
  }

  componentDidMount() {
    const url = 'https://api.nivesh.com/API/GetSummaryDetails';
    const postBody = {
      TotalClient: "",
      TotalPartner: "",
      TotalPartnerOverCities: "",
      NoOfTransaction: "",
      TotalTransactionValue: ""

    };
    const requestMetadata = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'token': '636F8F63-06C4-4D95-8562-392B34025FB0'
      },
      body: JSON.stringify(postBody)
    };

    fetch(url, requestMetadata)
      .then(response => response.json())
      .then(data => {
        this.setState({items: data.SummaryDetailsList });
      }).catch(error => {
        console.log(error);
      });
  }

  formatNumber = (value) => {
    return (value).toLocaleString('en-IN', {
      maximumFractionDigits: 0
    })
  }

  render() {

    const { t } = this.props;
    const {items } = this.state;

    return (
      <main>
        <Helmet>
                <meta charSet="utf-8" />
                <title>{t('MetaTitle:mT75')}</title>
                <meta name="description" content={t('MetaDescription:mD75')} />
            </Helmet>
        <div className="container container-wrapper">
          <div className="row">

            <div className="col-lg-8 col-md-6 col-12">
              <h1>
                {t("GrowMF:GMF1")}
                <br/>
                {t("GrowMF:GMF2")}
              </h1>
              <p>{t("GrowMF:GMF3")}</p>

              <div className="col-lg-6 mb-4">
                <div className="card">
                  <div className="card-body">
                    <h3>{items ? this.formatNumber(items.TotalPartner):"Loading..."}</h3>
                    <p>{t("Home:hOME61")} <span>{items ? this.formatNumber(items.TotalPartnerOverCities):"Loading..."}</span> {t("Home:hOME61-A")}</p>
                  </div>
                </div>
              </div>
              <div className="col-lg-6 mb-4">
                <div className="card">
                  <div className="card-body">
                    <h3>{items ? this.formatNumber(items.TotalClient):"Loading..."}</h3>
                    <p>{t("Home:hOME62")}</p>
                  </div>
                </div>
              </div>
              <div className="col-lg-6 mb-4">
                <div className="card">
                  <div className="card-body">
                    <h3>{items ? this.formatNumber(items.NoOfTransaction):"Loading..."}</h3>
                    <p>{t("Home:hOME63")}</p>
                  </div>
                </div>
              </div>
              <div className="col-lg-6 mb-4">
                <div className="card">
                  <div className="card-body">
                    <h3>Rs. {items ? this.formatNumber(parseInt(items.TotalTransactionValue)):"Loading..."}</h3>
                    <p>{t("Home:hOME64")}</p>
                  </div>
                </div>
              </div>
            </div>

            <Form />
          </div>
        </div>

        <div className="landingPlatforms">
          <div className="container">
            <div className="row">
              <div className="col-lg-12">
                <h2>{t("GrowMF:GMF4")}</h2>
                <h2>{t("GrowMF:GMF5")}</h2>
                <p>
                  {t("GrowMF:GMF6")}
                  <br />
                  <br />
                  {t("GrowMF:GMF7")}
                </p>
              </div>
            </div>
          </div>
        </div>

        <div className="container container-wrapper">
          <div className="row">
            <div className="col-lg-12">
              <h2>{t("GrowMF:GMF8")}</h2>
              <ul>
                <li>{t("GrowMF:GMF9")}</li>
                <li>{t("GrowMF:GMF10")}</li>
                <li>{t("GrowMF:GMF11")}</li>
                <li>{t("GrowMF:GMF12")}</li>
              </ul>
              <div className="text-left">
                <a href="#" title="" className="btn btn-primary">
                  {t('CTAFooter:JoinUs1')}
                </a>
              </div>
            </div>
          </div>
        </div>

        <div className="bg-light">
          <div className="container container-wrapper">
            <div className="row">
              <div className="col-lg-12 getStartedBtn">
                <h2>{t("GrowMF:GMF13")}</h2>
                <ul>
                  <li>{t("GrowMF:GMF14")}</li>
                  <li>{t("GrowMF:GMF15")}</li>
                  <li>{t("GrowMF:GMF16")}</li>
                  <li>{t("GrowMF:GMF17")}</li>
                  <li>{t("GrowMF:GMF18")}</li>
                </ul>
              </div>
            </div>
          </div>
        </div>

        <div className="container container-wrapper">
          <div className="row">
            <div className="col-lg-12">
              <h2>{t("GrowMF:GMF19")}</h2>
              <ul>
                <li>{t("GrowMF:GMF20")}</li>
                <li>{t("GrowMF:GMF21")}</li>
                <li>{t("GrowMF:GMF22")}</li>
              </ul>
              <div className="text-left">
                <a href="#" title="" className="btn btn-primary">
                  {t('CTAFooter:JoinUs1')}
                </a>
              </div>
            </div>
          </div>
        </div>

        <Testimonials />
      </main>
    );
  }
}

export default withTranslation()(Grow);
